"use client";

import Link from "next/link";
import Image from "next/image";
import { useRouter } from "next/navigation";
import { useState } from "react";
import { FaBars, FaTimes } from "react-icons/fa";
import AuthForm from "@/components/AuthForm";
import useAuthStore from "@/store/authStore";

export default function NavBar() {
  const router = useRouter();
  const { user, isAuthenticated, logout } = useAuthStore();
  const [menuOpen, setMenuOpen] = useState(false);
  const [showLogin, setShowLogin] = useState(false);

  const navLinks = [
    { label: "Home", href: "/" },
    { label: "Start a fundraiser", href: "/startfundraiser" },
    { label: "Fundraised", href: "/fundraisers" },
    { label: "Food Partners", href: "/partners/food" },
    { label: "Health Partners", href: "/partners/health" },
    { label: "Become A Partner", href: "/partner" },
    { label: "Become A Volunteer", href: "/volunteer" },
  ];

  const handleClick = (path, modalSetter) => {
    if (path) router.push(path);
    if (modalSetter) modalSetter(true);
  };

  const handleLogout = async () => {
    try {
      await logout();
      router.push("/");
    } catch (error) {
      console.error("Logout error:", error);
    }
  };

  return (
    <>
      <header className="sticky top-0 z-50 bg-white/30 backdrop-blur-md shadow-lg transition-all duration-300">
        <div className="max-w-7xl mx-auto flex items-center justify-between px-2 py-2">
          
          <Link href="/" className="flex items-center gap-3">
            <Image
              src="/logo.webp"
              alt="Care Foundation Logo"
              width={200}
              height={85}
              className="rounded-md hover:scale-105 transition-transform duration-300"
            />
          </Link>

          <nav className="hidden md:flex items-center gap-6">
            {navLinks.slice(0, 1).map((link) => (
              <button
                key={link.label}
                onClick={() => handleClick(link.href)}
                className="text-black font-semibold text-lg hover:scale-105 transition"
              >
                {link.label}
              </button>
            ))}

            <div className="flex items-center gap-4">
              
              <div className="relative group">
                <button className="font-semibold text-black hover:scale-105 transition text-lg">
                  Crowd Funding
                </button>
                <div className="absolute top-full left-0 mt-2 w-52 bg-white rounded-lg shadow-lg opacity-0 group-hover:opacity-100 invisible group-hover:visible transition-all duration-300 z-50">
                  <button
                    onClick={() => handleClick("/startfundraiser")}
                    className="block text-black hover:scale-105 px-4 py-2 hover:bg-green-100 w-full text-left"
                  >
                    Start a Fundraiser
                  </button>
                  <button
                    onClick={() => handleClick("/fundraised")}
                    className="block text-black hover:scale-105 px-4 py-2 hover:bg-green-100 w-full text-left"
                  >
                    Fundraised
                  </button>
                  <button
                    onClick={() => handleClick("/fundraisers")}
                    className="block text-black hover:scale-105 px-4 py-2 hover:bg-green-100 w-full text-left"
                  >
                    Fundraiser
                  </button>
                </div>
              </div>

              <div className="relative group">
                <button className="font-semibold text-black hover:scale-105 transition text-lg">
                  Partners
                </button>
                <div className="absolute top-full left-0 mt-2 w-52 bg-white rounded-lg shadow-lg opacity-0 group-hover:opacity-100 invisible group-hover:visible transition-all duration-300 z-50">
                  <button
                    onClick={() => handleClick("/partners/food")}
                    className="block px-4 py-2 text-black hover:scale-105 w-full text-left"
                  >
                    Food Partners
                  </button>
                  <button
                    onClick={() => handleClick("/partners/health")}
                    className="block px-4 py-2 text-black hover:scale-105 w-full text-left"
                  >
                    Health Partners
                  </button>
                </div>
              </div>

              <div className="relative group">
                <button className="font-semibold text-black hover:scale-105 transition text-lg">
                  Join Us
                </button>
                <div className="absolute top-full left-0 mt-2 w-52 bg-white rounded-lg shadow-lg opacity-0 group-hover:opacity-100 invisible group-hover:visible transition-all duration-300 z-50">
                  <button
                    onClick={() => handleClick("/partner")}
                    className="block px-4 py-2 text-black hover:scale-105 w-full text-left"
                  >
                    Become A Partner
                  </button>
                  <button
                    onClick={() => handleClick("/volunteer")}
                    className="block px-4 py-2 text-black hover:scale-105 w-full text-left"
                  >
                    Become A Volunteer
                  </button>
                </div>
              </div>

              <div className="flex items-center gap-4">
                {isAuthenticated ? (
                  <>
                    <button
                      onClick={handleLogout}
                      className="border-2 border-red-500 text-red-600 px-6 py-2 rounded-md font-semibold hover:bg-red-500 hover:text-white transition-all duration-300"
                    >
                      Logout
                    </button>
                  </>
                ) : (
                  <button
                    onClick={() => handleClick(undefined, setShowLogin)}
                    className="border-2 border-green-500 text-green-600 px-6 py-2 rounded-md font-semibold hover:bg-green-500 hover:text-white transition-all duration-300"
                  >
                    Sign In
                  </button>
                )}
              </div>
            </div>
          </nav>

          <button
            className="md:hidden text-gray-700 text-3xl focus:outline-none"
            onClick={() => setMenuOpen(!menuOpen)}
          >
            {menuOpen ? <FaTimes /> : <FaBars />}
          </button>
        </div>

        {menuOpen && (
          <div className="md:hidden bg-white shadow-lg border-t border-gray-200 px-6 py-5 space-y-3 animate-fadeIn">
            {navLinks.map((link) => (
              <button
                key={link.label}
                onClick={() => {
                  handleClick(link.href);
                  setMenuOpen(false);
                }}
                className="block w-full text-left text-gray-700 font-semibold text-lg hover:text-green-500 transition py-2"
              >
                {link.label}
              </button>
            ))}

            <div className="pt-3 flex flex-col gap-3">
              {isAuthenticated ? (
                <>
                  <button
                    onClick={() => {
                      handleLogout();
                      setMenuOpen(false);
                    }}
                    className="border-2 border-red-500 text-red-600 px-5 py-2 rounded-md font-semibold hover:bg-red-500 hover:text-white transition text-center"
                  >
                    Logout
                  </button>
                </>
              ) : (
                <>
                  <button
                    onClick={() => {
                      setShowLogin(true);
                      setMenuOpen(false);
                    }}
                    className="border-2 border-green-500 text-green-600 px-5 py-2 rounded-md font-semibold hover:bg-green-500 hover:text-white transition text-center"
                  >
                    Sign In
                  </button>
                </>
              )}
            </div>
          </div>
        )}
      </header>

      {/* Auth Form Modal (Login & Register) */}
      {showLogin && (
        <div className="fixed inset-0 z-[999] bg-white overflow-auto">
          <button
            onClick={() => setShowLogin(false)}
            className="absolute top-3 right-3 text-gray-700 text-xl hover:text-red-500 z-50"
          >
            ✕
          </button>
          <AuthForm />
        </div>
      )}
    </>
  );
}


